import React from 'react'
import { BiPlus } from 'react-icons/bi'
import styled from 'styled-components'
import openColor from '../../lib/openColor'

export type AccountCreateSectionProps = {
  keyword: string
}

function AccountCreateSection({ keyword }: AccountCreateSectionProps) {
  return (
    <Block>
      <CreateIcon>
        <BiPlus />
      </CreateIcon>
      <KeywordBlock>"{keyword}" 연락처 만들기</KeywordBlock>
    </Block>
  )
}

export default AccountCreateSection

const Block = styled.div`
  padding: 8px 16px;
  background-color: ${openColor.gray[1]};
  /* border-bottom: 1px solid ${openColor.gray[2]}; */
  display: flex;
  align-items: center;
  cursor: pointer;
  position: fixed;
  width: 100%;
  z-index: 50;
`
const CreateIcon = styled.div`
  min-width: 28px;
  min-height: 28px;
  border-radius: 50%;
  border: 1px solid ${openColor.gray[6]};
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 10px;
  background-color: #fff;
  svg {
    color: ${openColor.gray[6]};
    font-size: 18px;
  }
`
const KeywordBlock = styled.div`
  margin-left: 24px;
`
